.icons-container {
    position: absolute;
    top: 10px;
    right: 0px;
    width: 45px;
    height: 45px;
    overflow: hidden;
}

.icon-search {
    position: relative;
    top: 5px;
    left: 8px;
    width: 50%;
    height: 50%;
    opacity: 1;
    border-radius: 50%;
    border: 3px solid #c7d0f8;
    transition: opacity 0.25s ease, -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
    -webkit-transition: opacity 0.25s ease, -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
    transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
    transition: opacity 0.25s ease, transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1), -webkit-transform 0.43s cubic-bezier(0.694, 0.048, 0.335, 1);
}

.icon-search:after {
    content: "";
    position: absolute;
    bottom: -9px;
    right: -2px;
    width: 4px;
    border-radius: 3px;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    height: 10px;
    background-color: #c7d0f8;
}

.field {
    border: 0;
    width: 100%;
    height: 100%;
    font-size: 1.3em;
    padding: 10px 15px;
    padding-right: 45px;
    background: white;
    border-radius: 3px;
    box-shadow: 0px 8px 15px rgba(75, 72, 72, 0.1);
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
}

.field:focus {
    outline: none;
    box-shadow: 0px 9px 20px rgba(75, 72, 72, 0.3);
}

.field:focus+.icons-container .icon-search {
    opacity: 0;
    -webkit-transform: translateX(200%);
    transform: translateX(200%);
}


