/* Line With Text */

div.background {
    color: #17A2B7;
    position: relative;
    z-index: 1;
    margin-top: 20px;
    text-align: center;
}

div.background:before {
    border-top: 1.5px solid #17A2B7;
    content: "";
    margin: 0 auto;
    /* this centers the line to the full width specified */
    position: absolute;
    /* positioning must be absolute here, and relative positioning must be applied to the parent */
    top: 50%;
    left: 0;
    right: 0;
    bottom: 0;
    width: 99.5%;
    z-index: -1;
}

div.background span {
    /* to hide the lines from behind the text, you have to set the background color the same as the container */
    background: #fff;
    padding: 0 15px;
}

/* --------------------------------- Line with Text --> answer   ------------------------------------------------------ */

div.background-answer {
    color: #fc6076;
    position: relative;
    z-index: 1;
    margin-top: 20px;
    text-align: center;
}

div.background-answer:before {
    border-top: 1.5px solid #fc6076;
    content: "";
    margin: 0 auto;
    /* this centers the line to the full width specified */
    position: absolute;
    /* positioning must be absolute here, and relative positioning must be applied to the parent */
    top: 50%;
    left: 0;
    right: 0;
    bottom: 0;
    width: 99.5%;
    z-index: -1;
}

div.background-answer span {
    /* to hide the lines from behind the text, you have to set the background color the same as the container */
    background: #fff;
    padding: 0 15px;
}

